.single-article{
  &__wrapper{
    padding: 15px 23px;
    width: 100%;
    max-width: 780px;


    background: #FFFFFF;
    border-radius: 8px;

    position: relative;
  }
  &__header{
    width: 100%;
  }
  &__img{
    height: 400px;
    &-item{
      width: 100%;
      height: 100%;

      object-fit: contain;
    }
  }
  &__info{
    display: flex;
    align-items: center;
    justify-content: space-between;

    margin-top: 8px;
    &-text{
      font-family: "Acrom", sans-serif;
      font-size: 14px;

      color: #898989;
    }
  }
  &__content{
    margin-top: 15px;
  }
  &__title{
    font-family: 'Merriweather', sans-serif;
    font-weight: 700;
    @include fluid-type($min_width, $max_width, 32px, 42px);
    line-height: 110%;
    color: #2A2A2A;
  }
  &__desc{
    margin-top: 15px;
    p {
      margin-bottom: 10px;
      font-family: 'Acrom', sans-serif;
      font-weight: 400;
      font-size: 18px;
      line-height: 125%;

      color: #000000;
    }
    blockquote{
      font-family: 'Acrom', sans-serif;
      font-weight: 400;
      font-size: 18px;
      line-height: 125%;

      margin: 15px 0;

      background: #F5F5FB;

      padding: 20px;
    }
  }
}

@media screen and (max-width: 900px) {
  .single-article{
    &__img{
      height: auto;
    }
  }
}

@media screen and (max-width: 600px) {
  .single-article{
    &__wrapper{
      padding: 0 18px 30px 18px;
    }
    &__desc{
      blockquote{
        padding: 13px 15px;
      }
    }
  }
}