.aside{
  height: 100%;
  width: 100%;
  &__wrapper{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;

    width: 100%;
    height: 100%;

    padding: 30px 25px 50px 25px;

    overflow-y: auto;

    background: #fff;
    &-block{
      height: 100%;
      display: flex;
      flex-direction: column;

      padding-top: 50px;
    }
  }
  &__footer{
    margin-top:auto;
    padding-top: 20px;
    width: 100%;
  }
  &__burger{
    display: none;
  }
  &__btn{
    height: 40px;
    max-width: 165px;
    flex-shrink: 0;
  }
  &-logo{
    height: 30px;

    display: flex;
    align-items: center;
    justify-content: center;
    &__img{
      width: 100%;
      height: 100%;

      object-fit: contain;
    }
    &__text{
      font-family: "Montserrat", sans-serif;
      font-weight: 600;
      color:#4060FA;
      font-size: 22px;
    }
  }
  &-nav{
    &__item{
      &-has-child{
        position: relative;
        &.active{
          &>.aside-nav__link{
            &::after{
              transform: rotate(180deg);
            }
          }
        }
       &>.aside-nav__link{
         &::after{
           content: '';
           position: relative;
           width: 10px;
           height: 10px;
           display: block;
           margin-left: 10px;
           background: url("../img/arrow-gray.svg") center center/contain no-repeat;

           transition: .3s all linear;
         }
         &.active{
           &::after{
             background: url("../img/arrow-gray.svg") center center/contain no-repeat;
           }
         }
       }
      }
      &.active{
        &>.aside-nav__list{
          display: block;
        }
      }
    }
    &__list{
      .aside-nav__list{
        padding-left: 40px;

        display: none;
        .aside-nav__list{
          padding-left: 0px;
        }
      }
    }
    &__link{
      display: flex;
      align-items: center;
      color:#4F5D71;
      padding: 6.5px 0;
      &-text{
        font-family: 'Acrom', sans-serif;
        font-size: 14px;
        font-weight: 500;
        letter-spacing: -0.01em;

        color: inherit;
      }
      &-icon{
        width: 16px;
        height: 16px;

        flex-shrink: 0;
        svg{
          width: 100%;
          height: 100%;
          path{
            fill: #126DE9;
          }
        }
      }
    }
  }
}
.page{
  &-list{
    &__item + &__item{
      margin-top: 8px;
    }
    &__link{
      padding: 10px 12px;
      position: relative;
      &::before{
        content: '';
        position: absolute;
        left: -20px;
        top: 0;
        width: 4px;
        height: 100%;
        background: #126DE9;

        opacity: 0;
      }
      &.active{
        background: #fff;
        border-radius: 8px;
        color: #126DE9;
        &::before{
          opacity: 1;
        }
        svg{
          path{
            fill: #126DE9;
          }
        }
      }
      &-text{
        font-weight: 500;
        color:inherit;
        margin-left: 10px;
      }
    }
  }
}

.burger{
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;

  width: 24px;
  height: 16px;
  position: relative;
  &__item{
    flex-shrink: 0;

    width: 100%;
    height: 1.5px;
    border-radius: 3px;
    background: #126DE9;

    transition: 0.5s all;
  }
  &:hover{
    cursor: pointer;
  }
  &.active{
    transform: translateY(8px);
    .burger{
      &__item{
        position: absolute;
        &:nth-child(2){
          display: none;
        }
        &:nth-child(1){
          left: 0;
          top: 0;

          transform: rotate(45deg);
        }
        &:nth-child(3){
          right: 0;
          top: 0;

          transform: rotate(-45deg);
        }
      }
    }
  }
}


@media screen and (max-width: 991px) {
  .aside{
    box-shadow: 0px 1px 19px rgba(113, 128, 168, 0.16);
    &.active{
      .aside{
        &__wrapper{
          &-block{
            background: #fff;

            transform: translateX(0);
            display: block
          }
        }
        &__content,&__footer{
          display: block;
        }
      }
    }
    &__wrapper{
      padding: 0;

      background: none;
      &-block{
        width: 100%;
        padding: 30px 72px 60px 72px;
        transform: translateX(-150%);

        transition: 0.5s linear;

        max-height: calc(100vh - 60px);
        overflow-y: auto;

        display: none;
      }
    }
    &__header{
      height: 60px;

      display: flex;
      align-items: center;
      justify-content: flex-end;
      width: 100%;

      position: relative;

      padding: 0 15px;

      background: #fff;
    }
    &__burger{
      display: block;

      position: absolute;
      left: 15px;
      top: calc(50% - 8px);
    }
    &__content,&__footer{
      width: 100%;
    }
    &__footer{
      padding-top: 30px;
    }
    &__btn{
      margin: 0 auto;
      max-width: 230px;
    }
  }
  .page{
    &-list{
      &__link{
        &::before{
          display: none;
        }
      }
    }
  }
}