.card{
  &-grid{
    &__wrapper{
      display: grid;
      grid-template-columns: repeat(4,minmax(0,1fr));
      gap: 40px 25px;

      grid-auto-rows:230px  /*calc((100vw - 256px - 100px) / 4)*/;
      &_small{
        grid-auto-rows:200px;
      }
    }
    &__item{
      &_column{
        &-medium{
          grid-column-end: span 2;
        }
      }
    }
    &__footer{
      margin-top: 35px;

      display: flex;
      align-items: center;
      justify-content: flex-end;
      &_margin{
        &-small{
          margin-top: 15px;
        }
      }
      &_position{
        &-center{
          justify-content: center;
        }
      }
    }
    &__link{
      font-family: 'Acrom', sans-serif;
      font-size: 14px;
      letter-spacing: -0.01em;

      color: #126DE9;
    }
    &__btn{
      max-width: 195px;
      height: 45px;
    }
  }
  &-item{
    &__wrapper{
      position: relative;
      height: 100%;

      background: #fff;
      border-radius: 8px;
    }
    &__header{
      height: 65%;
      display: flex;
      align-items: flex-end;
      justify-content: center;


      position: relative;

      border-top-right-radius: 8px;
      border-top-left-radius: 8px;
      overflow: hidden;
      &_size{
        &-small{
          padding-top: 10px;
          height: 115px;

          border-bottom: 1px solid #F5F5FB;
        }
      }
      &-stars{
        position: absolute;
        left: 9px;
        top: 7px;
      }
      &-footer{
        position: absolute;
        left: 0;
        bottom: 0;

        display: flex;
        align-items: flex-end;
      }
    }
    &__stars{
      display: grid;
      grid-template-columns: repeat(5,minmax(0,12px));
      gap: 1px;
    }
    &__star{
      width: 12px;
      height: 12px;
      &-img{
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }
    &__category{
      width: 32px;
      height: 18px;

      display: flex;
      align-items: center;
      justify-content: center;

      background: #9EC6F7;
      &-icon{
        width: 14px;
        height: 14px;
      }
    }
    &__tags{
      display: flex;
      align-items: center;
      margin-left: 4px;
    }
    &__tag + &__tag{
      margin-left: 4px;
    }
    &__tag{
      display: flex;
      align-items: center;
      justify-content: center;

      width: 40px;
      height: 18px;

      border-radius: 10px;
      &_color{
        &-yellow{
          background:#F7ECD1
        }
        &-purple{
          background:#F9D1EF;
        }
      }
      &-text{
        font-family: 'Acrom', sans-serif;
        font-size: 12px;
        letter-spacing: -0.01em;

        color: #4F4F4F;
      }
    }
    &__img{
      width: 100%;
      height: 100%;
      object-fit: contain;
      &_size{
        &-full{
          object-fit: cover;
        }
      }
    }
    &__content{
      padding: 15px 15px 20px 15px;
      &_size{
        &-small{
          padding: 8px 10px 0px 10px;
        }
      }
    }
    &__title{
      font-family: "Merriweather", sans-serif;
      font-weight: 700;
      color:#2A2A2A;
      @include fluid-type($min_width, $max_width, 16px, 18px);
      line-height: 1.2;
      &_position{
        &-center{
          text-align: center;
        }
      }
    }
    &__btn{
      position: absolute;
      bottom: -17.5px;
      left: calc(50% - 67.5px);
      width: 135px;
      height: 35px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .card {
    &-grid {
      &__wrapper {
        grid-template-columns: repeat(3,minmax(0,1fr));
      }
      &__footer{
        justify-content: center;
      }
      &__link{

        background: #D8E9FE;
        border-radius: 4px;

        max-width: 345px;
        width: 100%;
        height: 35px;

        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
  }
}

@media screen and (max-width: 700px) {
  .card {
    &-grid {
      &__wrapper {
        grid-template-columns: repeat(2,minmax(0,1fr));
      }
      &__btn{

      }
    }
    &-item{
      &__title{
        text-align: left;
      }
    }
  }
}


@media screen and (max-width: 475px) {
  .card {
    &-grid {
      &__wrapper {
        grid-template-columns: minmax(0,1fr);

        gap: 32px;
      }
    }
  }
}